@import '../../styles/mixins';

.sb-multiselect__menu-portal {
  z-index: calc(var(--sbt-stack-modal) + 100) !important;
}

//need to scope multiselect overrides under sb-multiselect per react-select prefix
.sb-multiselect.sb-multiselect,
.sb-multiselect__menu-portal {
  &:focus {
    border: var(--sbt-stroke-thin-blue-60);
    box-shadow: none;
    outline: none;
  }

  .sb-multiselect__menu {
    width: var(--sb-multiselect-menu-width, 100%);
  }

  .sb-multiselect__placeholder {
    color: var(--sb-select__placeholder, var(--sbt-theme-tertiary-color-30));
    font-size: var(--sb-input-text-size, var(--sbt-font-size-30));
    font-weight: var(--sb-input-text-weight, var(--sbt-font-weight-40));
    line-height: var(--sbt-line-height-100);
  }

  .sb-multiselect__indicator-separator {
    background-color: var(
      --sb-select-separator-color,
      var(--sbt-theme-tertiary-color-30)
    );
  }

  .sb-multiselect__control {
    border: var(--sbt-input-border-size) solid var(--sbt-input-border-color);
    border-radius: var(--sbt-input-border-radius);
    min-height: var(--sbt-input-height);
  }

  .sb-multiselect__indicator {
    color: var(--sb-select-indicator-color, var(--sbt-theme-tertiary-color-30));
    padding: 0.5rem;

    &:hover {
      color: var(--sbt-theme-tertiary-color-50);
    }
  }

  .sb-multiselect__control--is-focused {
    border-color: transparent;
    box-shadow: var(--sbt-input-box-shadow);
    outline: none;

    &:hover {
      border-color: var(--sbt-theme-color-60);
    }
  }

  .sb-multiselect__option {
    background-color: transparent;
    border: var(--sb-select__option-border, none);
    color: var(--sbt-theme-tertiary-color-80);
    cursor: pointer;
    font-size: var(--sb-input-text-size, var(--sbt-font-size-30));
    font-weight: var(--sb-input-text-weight, var(--sbt-font-weight-40));
    line-height: var(--sbt-line-height-50);
    padding: 0.5rem;
    transition: 0.2s ease;
  }

  .sb-multiselect__option--is-focused.sb-multiselect__option--is-focused {
    background-color: var(
      --sb-multiselect__focused-background-color,
      var(--sbt-theme-color-20)
    );
  }

  .sb-multiselect__option--is-selected.sb-multiselect__option--is-selected {
    background-color: var(
      --sb-multiselect__selected-background-color,
      var(--sbt-theme-color-50)
    );
    color: var(--sb-multiselect__selected-text-color, var(--sbt-color-text));

    &:hover {
      background-color: var(
        --sb-multiselect__selected-focused-background-color,
        var(--sbt-theme-color-30)
      );
      color: var(
        --sb-multiselect__selected-focused-text-color,
        var(--sbt-color-text)
      );
    }
  }

  .sb-multiselect--is-disabled {
    // NOTE: overriding to allow disabled-shake which depends on hover event
    pointer-events: initial;
  }

  .sb-multiselect__control--is-disabled {
    @include disabled-shake();

    background-color: var(--sbt-color-disabled-background);
    border-color: var(--sbt-color-disabled-border);
    cursor: not-allowed;

    .sb-multiselect__placeholder {
      color: var(--sbt-color-disabled-text);
    }
  }

  .sb-multiselect__option--is-disabled {
    color: var(--sbt-color-disabled-text);
    cursor: not-allowed;

    &:hover {
      background-color: transparent;
    }
  }

  .sb-multiselect__multi-value {
    background-color: var(
      --sb-multi-value-background-color,
      var(--sbt-color-background)
    );
    border: 1px solid var(--sbt-input-border-color);
    border-radius: var(--sbt-border-radius-large);
  }

  .sb-multiselect__multi-value__label {
    color: var(--sb-multivalue-text-color, var(--sbt-theme-color-60));
    font-size: var(--sb-input-text-size, var(--sbt-font-size-25));
    font-weight: var(--sb-input-text-weight, var(--sbt-font-weight-40));
    line-height: var(--sbt-line-height-25);
  }

  .sb-multiselect__multi-value__remove {
    background-color: var(
      --sb-multi-value-background-color,
      var(--sbt-color-background)
    );
    border-radius: var(--sbt-border-radius-large);
    color: var(--sb-multivalue-remove-color, var(--sbt-theme-color-60));
  }

  .sb-multiselect__multi-value--is-disabled {
    .sb-multiselect__multi-value__label {
      color: var(
        --sb-multiselect__multi-value-text-color_disabled,
        var(--sbt-theme-tertiary-color-40)
      );
    }

    .sb-multiselect__multi-value__remove {
      color: var(
        --sb-multiselect__multi-value-remove-color_disabled,
        var(--sbt-theme-tertiary-color-40)
      );
    }
  }

  .sb-multiselect__multi-value__remove:hover {
    background-color: var(--sbt-color-background);
    color: var(--sbt-theme-color-90);
  }

  .sb-multiselect__control--is-focused.sb-multiselect__control--is-focused {
    border-color: transparent;
    box-shadow: var(--sbt-input-box-shadow);
    outline: none;
  }
}

.sb-multiselect {
  &.sb-multiselect__flush-right .sb-multiselect__control {
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
  }

  &.sb-multiselect__flush-left .sb-multiselect__control {
    border-bottom-left-radius: 0;
    border-top-left-radius: 0;
  }

  &.sb-multiselect__flush .sb-multiselect__control {
    border-radius: 0;
  }
}

.sb-multiselect__group-selected {
  .sb-multiselect__group {
    border-bottom: 0.25rem solid var(--sbt-input-border-color);
    padding-bottom: 0;
    padding-top: 0;
  }

  .sb-multiselect__group:last-of-type {
    border-bottom: 0;
  }

  .sb-multiselect__group-heading {
    display: none;
  }
}

.sb-multiselect__clear-indicator {
  color: var(--sbt-theme-tertiary-color-30);
  cursor: pointer;
  font-size: var(--sb-input-text-size, var(--sbt-font-size-25));
  font-weight: var(--sb-input-text-weight, var(--sbt-font-weight-40));
  padding: 0 0.5rem;
}

.sb-multiselect__clear-indicator:hover {
  color: var(--sbt-theme-tertiary-color-60);
}

.sb-multiselect__loading-indicator {
  padding: 0.375rem;
}

// Multi Select with custom icon
// --sb-multiselect-icon-color
.sb-multiselect__icon-container {
  color: var(--sb-multiselect__icon-color, var(--sbt-theme-tertiary-color-30));
  margin-right: 0.375rem;

  &:hover {
    color: var(--sbt-theme-tertiary-color-50);
  }
}

.sb-multiselect__value-container-content {
  display: flex;
  flex: 1;
  flex-wrap: wrap;
}

.sb-multiselect__select-all {
  border-bottom: var(--sbt-input-border-size) solid
    var(--sbt-input-border-color);
  cursor: pointer;
  line-height: var(--sbt-line-height-50);
  padding: var(--sbt-spacing-20);
  position: relative;

  .sb-link {
    font-size: var(--sb-input-text-size, var(--sbt-font-size-30));
    font-weight: var(--sb-input-text-weight, var(--sbt-font-weight-40));
  }
}

.sb-multiselect.sb-multiselect_error {
  @include error();
  border: none;
  padding: var(--sbt-spacing-0);

  .sb-multiselect__control,
  .sb-multiselect__control--is-focused.sb-multiselect__control--is-focused {
    border: var(--sbt-stroke-thin-error);
    box-shadow: var(--sbt-elevation-0);

    &:hover {
      box-shadow: var(--sbt-elevation-error);
    }
  }
}

.sb-multiselect_with-checkboxes {
  --sb-multiselect__selected-background-color: transparent;
  --sb-multiselect__selected-text-color: var(--sbt-black);
  --sb-multiselect__selected-focused-background-color: var(
    --sbt-theme-color-20
  );
  --sb-multiselect__selected-focused-text-color: var(--sbt-black);
}
