.sb-multi-menu-list {
  --sb-checkbox-label-line-height: 1rem;
  width: 100%;

  &,
  .sb-multiselect,
  .sb-multiselect__menu,
  .sb-muiltiselect_menu-list-wrapper,
  .sb-multiselect__menu-list {
    height: 100%;
    max-height: initial;
    top: 0;
  }

  .sb-multiselect__control {
    display: none;
  }

  .sb-multiselect__menu {
    border-radius: 0;
    margin: 0;
    position: relative;
  }

  .sb-multiselect__menu-list {
    padding: 0;
  }

  /* stylelint-disable-next-line selector-max-compound-selectors */
  .sb-multiselect .sb-multiselect__option {
    padding: var(--sbt-spacing-30) var(--sbt-spacing-40);
  }

  .sb-multiselect__option--is-selected.sb-multiselect__option--is-selected:not(
      :hover
    ) {
    background-color: inherit;
    color: inherit;
  }
}

.sb-multi-menu-list__flat {
  --sb-select-menu-elevation: none;
  --sb-select-menu-background: transparent;
}
