.sb-multiselect-popout .sb-multiselect__value-container {
  flex-wrap: nowrap;
  font-size: var(--sb-input-text-size, var(--sbt-font-size-30));

  .sb-icon {
    margin-right: 0.375rem;
  }
}

.sb-multiselect_popout-actions {
  border-bottom: var(--sbt-input-border-size) solid
    var(--sbt-input-border-color);
  padding: 0.5rem;
}

.sb-multiselect_popout_value-container {
  color: var(--sb-multivalue-text-color, var(--sbt-theme-tertiary-color-50));
  overflow: clip visible;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.sb-multiselect_popout_value-placeholder {
  color: var(--sb-select__placeholder, var(--sbt-theme-tertiary-color-30));
  font-size: var(--sb-input-text-size, var(--sbt-font-size-30));
  font-weight: var(--sb-input-text-weight, var(--sbt-font-weight-40));
}

.sb-multiselect__control--is-disabled {
  .sb-multiselect_popout_value-placeholder,
  .sb-multiselect_popout_value-container {
    color: var(--sbt-color-disabled-text);
  }
}

.sb-multiselect-popout__group-selected {
  .sb-multiselect__group {
    border-bottom: 0.25rem solid var(--sbt-input-border-color);
    padding-bottom: 0;
    padding-top: 0;
  }

  .sb-multiselect__group:last-of-type {
    border-bottom: 0;
  }

  .sb-multiselect__group-heading {
    display: none;
  }
}

.sb-multiselect-popout--focused {
  .sb-multiselect__control--is-focused {
    border-color: var(--sbt-input-border-color) !important;
    box-shadow: none !important;
  }

  .sb-multiselect_popout-actions .sb-input {
    border-color: transparent;
    box-shadow: var(--sbt-input-box-shadow);
  }
}

.sb-multiselect_popout-select-all {
  cursor: pointer;
  display: block;
  line-height: 1.5rem;
  padding: 0.5rem 0;
}
